.Login{
    /* //    margin-top: 250px; */
        width: 400px;
        margin-top: 180px;
        margin-left: 580px;;
        padding: 20px;
        padding-bottom: 40px;
        align-items: center;
        text-align: center;
        font-size: 25px;
        background-color:  #080c10;
        
        box-shadow: rgba(74, 46, 73, 0.09) 0px 2px 1px, rgba(99, 51, 93, 0.09) 0px 4px 2px, rgba(69, 34, 62, 0.09) 0px 8px 4px, rgba(66, 28, 60, 0.09) 0px 16px 8px, rgba(80, 39, 77, 0.09) 0px 32px 16px;
    }
    
    .wr{
        display: flex;
        
        flex-direction: column;
        align-items: center;
        text-align: center;
     
    }
    
    .uname, .pass, .cpass, .email{
        margin: 5px;
       
        
    }
    
    .uname input,  .pass input, .cpass input, .email input{
    height: 43;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    font-size: 16px;
    text-align: left;
    border-radius: 5px;
    background: #070A0E;
    border-color: #CC57C7;
    
    }
    
    
    
    ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: #CC57C7;
        opacity: 5; /* Firefox */
      }
      
      :-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: #CC57C7;
      }
      
      ::-ms-input-placeholder { /* Microsoft Edge */
        color: #CC57C7;
      }
    
    
    
    .button20{
        margin-top: 10px;
    }
    
    .Login button {
        align-items: center;
        appearance: none;
        background-color: #CC57C7;
      border-radius: 4px;
      border-width: 0;
      box-shadow: 0 2px 4px rgba(74, 52, 73, 0.5), 0 7px 13px -3px rgba(87, 65, 86, 0.5), 0 -3px 0 inset rgba(77, 57, 76, 0.5);
      box-sizing: border-box;
      color: rgb(21, 21, 21);
      
        cursor: pointer;
        display: inline-flex;
        font-family: 'MyFont';
        height: 30px;
        justify-content: center;
        line-height: 1;
        list-style: none;
        overflow: hidden;
        padding-left: 12px;
        padding-right: 12px;
        position: relative;
        text-align: left;
        text-decoration: none;
        transition: box-shadow .15s,transform .15s;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        white-space: nowrap;
        will-change: box-shadow,transform;
        font-size: 18px;
        width : 25%
      }
      
      .Login button:focus {
        box-shadow: rgba(74, 52, 73, 0.5) 0 0 0 1.5px inset, rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(74, 52, 73, 0.5) 0 -3px 0 inset;
      }
      
      .Login button:hover {
        box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(74, 52, 73, 0.5) 0 -3px 0 inset;
        transform: translateY(-2px);
      }
      
      .Login button:active {
        box-shadow: rgba(74, 52, 73, 0.5) 0 3px 7px inset;
        transform: translateY(2px);
      }
    
    
    #alr {
        font-size: 17px;
    }



    @media screen and (max-width: 800px) {
      .Login {
        width: 78%;
        margin: auto;
        margin-top: 50vh;
        transform: translateY(-50%);
        padding: 20px;
      }
    
      .Login form {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    
      .Login input {
        width: 100%;
      }
    
      .Login button {
        width: 100%;
      }
/* 
      .Login  button{
        width: 100%;
      } */
      
    }
    